.App {
    display: flex;
}

.right {
    display: flex;
    flex-wrap: wrap;
    width: 1144px;
    margin: 0 0 0 114px;
    font-size: 14px;
    &__header{
        flex-basis: 100%;
    }
    &__formula{
        &__button{
            &__wrapper{
                display: flex;
                justify-content: flex-end;
                width: 100%;
                & > button {
                    padding: 5px;
                    line-height: 1.3 !important;
                    border-radius: 6px !important;
                    background-color: rgba(8, 31, 73, 1);
                    color: white;
                    &:hover{
                        color: white !important;
                    }
                  }
            }
        }
    }
}

.ant-modal-footer {
    &> a{
        &> button{
            margin-right: 8px;
            background-color: rgba(8, 31, 73, 1);
            color: white;
            &:hover{
                color: white !important;
            }
        }
    }
}